"use client";

import React from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";

interface Node {
  id: string;
  label: string;
  category: string;
  details: {
    definition: string;
    examples: string[];
    references: string[];
  };
}

interface NodeDetailModalProps {
  node: Node;
  onClose: () => void;
}

const NodeDetailModal: React.FC<NodeDetailModalProps> = ({ node, onClose }) => {
  return (
    <Dialog open onOpenChange={onClose}>
      <DialogContent className="max-w-2xl">
        <DialogHeader>
          <DialogTitle className="text-xl font-semibold flex items-center gap-2">
            <span 
              className="w-3 h-3 rounded-full inline-block"
              style={{
                backgroundColor: node.category === 'Primary' ? '#3b82f6' : '#10b981'
              }}
            />
            {node.label}
          </DialogTitle>
          <DialogDescription>
            Category: {node.category}
          </DialogDescription>
        </DialogHeader>

        <ScrollArea className="max-h-[60vh]">
          <div className="space-y-6 p-1">
            {/* Definition Section */}
            <div>
              <h3 className="font-semibold text-lg mb-2">Definition</h3>
              <p className="text-gray-700">
                {node.details.definition}
              </p>
            </div>

            {/* Examples Section */}
            {node.details.examples.length > 0 && (
              <div>
                <h3 className="font-semibold text-lg mb-2">Examples</h3>
                <ul className="list-disc pl-5 space-y-2">
                  {node.details.examples.map((example, index) => (
                    <li key={index} className="text-gray-700">
                      {example}
                    </li>
                  ))}
                </ul>
              </div>
            )}

            {/* References Section */}
            {node.details.references.length > 0 && (
              <div>
                <h3 className="font-semibold text-lg mb-2">References</h3>
                <ul className="space-y-2">
                  {node.details.references.map((reference, index) => (
                    <li key={index}>
                      <a
                        href={reference}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-blue-600 hover:underline break-all"
                      >
                        {reference}
                      </a>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        </ScrollArea>

        <div className="flex justify-end mt-4">
          <Button variant="outline" onClick={onClose}>
            Close
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default NodeDetailModal;
