"use client";

import React, { useState, useRef, useEffect } from 'react';
import { Card } from '@/components/ui/card';
import NodeDetailModal from './NodeDetailModal';
import { Button } from '@/components/ui/button';
import './concept-mapper.css';

interface Node {
  id: string;
  label: string;
  category: string;
  details: {
    definition: string;
    examples: string[];
    references: string[];
  };
}

interface Edge {
  source: string;
  target: string;
}

interface MapData {
  nodes: Node[];
  edges: Edge[];
}

interface MapDisplayPanelProps {
  mapData: MapData | null;
}

const MapDisplayPanel: React.FC<MapDisplayPanelProps> = ({ mapData }) => {
  const [selectedNode, setSelectedNode] = useState<Node | null>(null);
  const [scale, setScale] = useState(1);
  const [position, setPosition] = useState({ x: 450, y: 300 });
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (mapData && containerRef.current) {
      const container = containerRef.current;
      setPosition({
        x: container.clientWidth / 2,
        y: container.clientHeight / 2
      });
      setScale(1);
    }
  }, [mapData]);

  const handleMouseDown = (e: React.MouseEvent) => {
    if (e.target === containerRef.current || (e.target as HTMLElement).classList.contains('concept-map-container')) {
      setIsDragging(true);
      setDragStart({
        x: e.clientX - position.x,
        y: e.clientY - position.y,
      });
    }
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (isDragging) {
      setPosition({
        x: e.clientX - dragStart.x,
        y: e.clientY - dragStart.y,
      });
    }
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  const handleWheel = (e: React.WheelEvent) => {
    e.preventDefault();
    const delta = e.deltaY > 0 ? -0.1 : 0.1;
    const newScale = Math.min(Math.max(0.5, scale + delta), 2);
    setScale(newScale);
  };

  const getCategoryColor = (category: string): string => {
    const colors: { [key: string]: string } = {
      Primary: '#3b82f6',    // Blue
      Secondary: '#10b981',  // Green
      default: '#6b7280',    // Gray
    };
    return colors[category] || colors.default;
  };

  const handleZoomIn = () => {
    setScale(prev => Math.min(prev + 0.1, 2));
  };

  const handleZoomOut = () => {
    setScale(prev => Math.max(prev - 0.1, 0.5));
  };

  const handleReset = () => {
    setScale(1);
    if (containerRef.current) {
      setPosition({
        x: containerRef.current.clientWidth / 2,
        y: containerRef.current.clientHeight / 2
      });
    }
  };

  if (!mapData) {
    return (
      <Card className="w-full h-full">
        <div className="h-full flex items-center justify-center p-8">
          <div className="text-center space-y-4">
            <div className="w-16 h-16 mx-auto border-2 border-gray-300 rounded-full flex items-center justify-center">
              <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 20l-5.447-2.724A1 1 0 013 16.382V5.618a1 1 0 011.447-.894L9 7m0 13l6-3m-6 3V7m6 10l4.553 2.276A1 1 0 0021 18.382V7.618a1 1 0 00-.553-.894L15 4m0 13V4m0 0L9 7" />
              </svg>
            </div>
            <div className="space-y-2">
              <p className="text-lg font-medium text-gray-900">No concept map generated yet</p>
              <p className="text-sm text-gray-500">Enter your text and click "Generate Mind Map" to create a visualization</p>
            </div>
          </div>
        </div>
      </Card>
    );
  }

  return (
    <Card className="w-full h-full relative">
      <div className="zoom-controls">
        <Button
          variant="outline"
          size="icon"
          onClick={handleZoomIn}
          className="zoom-button"
        >
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
          </svg>
        </Button>
        <Button
          variant="outline"
          size="icon"
          onClick={handleZoomOut}
          className="zoom-button"
        >
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 12H4" />
          </svg>
        </Button>
        <Button
          variant="outline"
          size="icon"
          onClick={handleReset}
          className="zoom-button"
        >
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
          </svg>
        </Button>
      </div>

      <div
        ref={containerRef}
        className="concept-map-container"
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
        onWheel={handleWheel}
      >
        <div
          className="concept-map-content"
          style={{
            transform: `translate(${position.x}px, ${position.y}px) scale(${scale})`
          }}
        >
          {/* Render edges */}
          <svg className="absolute inset-0 pointer-events-none" style={{ width: '100%', height: '100%' }}>
            {mapData.edges.map((edge, index) => {
              const sourceNode = mapData.nodes.find(n => n.id === edge.source);
              const targetNode = mapData.nodes.find(n => n.id === edge.target);
              
              if (!sourceNode || !targetNode) return null;

              const x1 = (parseInt(sourceNode.id) - 1) * 200;
              const y1 = 0;
              const x2 = (parseInt(targetNode.id) - 1) * 200;
              const y2 = 0;

              return (
                <g key={`edge-${index}`}>
                  <line
                    x1={x1}
                    y1={y1}
                    x2={x2}
                    y2={y2}
                    className="concept-edge"
                    stroke="#94a3b8"
                    strokeWidth="2"
                  />
                </g>
              );
            })}
          </svg>

          {/* Render nodes */}
          {mapData.nodes.map((node) => (
            <div
              key={node.id}
              onClick={() => setSelectedNode(node)}
              className="concept-node absolute p-4 rounded-lg shadow-lg cursor-pointer"
              style={{
                left: (parseInt(node.id) - 1) * 200,
                top: 0,
                transform: 'translate(-50%, -50%)',
                backgroundColor: getCategoryColor(node.category),
                color: 'white',
              }}
            >
              <div className="text-center">
                <span className="font-medium whitespace-nowrap">{node.label}</span>
                <div className="text-xs opacity-75 mt-1">{node.category}</div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {selectedNode && (
        <NodeDetailModal
          node={selectedNode}
          onClose={() => setSelectedNode(null)}
        />
      )}
    </Card>
  );
};

export default MapDisplayPanel;
