"use client";

import React, { useState } from 'react';
import TextInputPanel from './TextInputPanel';
import MapDisplayPanel from './MapDisplayPanel';
import { Button } from '@/components/ui/button';
import { toast } from 'sonner';

interface Node {
  id: string;
  label: string;
  category: string;
  details: {
    definition: string;
    examples: string[];
    references: string[];
  };
}

interface Edge {
  source: string;
  target: string;
}

interface MapData {
  nodes: Node[];
  edges: Edge[];
}

const ConceptMapperUI = () => {
  const [inputText, setInputText] = useState('');
  const [mapData, setMapData] = useState<MapData | null>(null);
  const [loading, setLoading] = useState(false);

  const handleGenerateMap = async () => {
    if (!inputText.trim()) {
      toast.error("Please enter some text to generate the concept map.");
      return;
    }

    setLoading(true);
    try {
      // Process the input text to generate nodes and edges
      const sentences = inputText.split(/[.!?]+/).filter(s => s.trim());
      const nodes: Node[] = [];
      const edges: Edge[] = [];
      let nodeId = 1;

      // Extract main concepts as nodes
      sentences.forEach((sentence) => {
        const words = sentence.trim().split(/\s+/);
        if (words.length < 3) return; // Skip very short sentences

        // Look for potential concepts (capitalized words or words after "is", "are")
        const concepts = words.filter((word, index) => {
          const w = word.trim();
          if (w.length < 3) return false;
          if (/^[A-Z]/.test(w)) return true; // Capitalized words
          if (index > 0 && (words[index - 1].toLowerCase() === "is" || words[index - 1].toLowerCase() === "are")) return true;
          return false;
        });

        concepts.forEach((concept) => {
          const cleanConcept = concept.replace(/[^a-zA-Z\s]/g, '');
          if (!nodes.some(n => n.label.toLowerCase() === cleanConcept.toLowerCase())) {
            nodes.push({
              id: String(nodeId++),
              label: cleanConcept,
              category: nodeId === 2 ? 'Primary' : 'Secondary',
              details: {
                definition: sentence.trim(),
                examples: [`Example of ${cleanConcept}`],
                references: [`https://example.com/reference/${cleanConcept.toLowerCase()}`]
              }
            });
          }
        });
      });

      // Create edges between consecutive nodes
      for (let i = 1; i < nodes.length; i++) {
        edges.push({
          source: nodes[i - 1].id,
          target: nodes[i].id
        });
      }

      // Add some cross-connections for more interesting visualization
      if (nodes.length > 2) {
        edges.push({
          source: nodes[0].id,
          target: nodes[nodes.length - 1].id
        });
      }

      const generatedData: MapData = { nodes, edges };

      setMapData(generatedData);
      toast.success("Concept map generated successfully!");
    } catch (error) {
      console.error('Error generating concept map:', error);
      toast.error("Failed to generate concept map. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col lg:flex-row gap-6 min-h-[800px] p-6">
      <div className="w-full lg:w-2/5 flex flex-col">
        <TextInputPanel 
          inputText={inputText} 
          setInputText={setInputText}
          onGenerate={handleGenerateMap}
          loading={loading}
        />
      </div>
      <div className="w-full lg:w-3/5 bg-gray-50 rounded-lg border min-h-[800px] overflow-hidden">
        <MapDisplayPanel mapData={mapData} />
      </div>
    </div>
  );
};

export default ConceptMapperUI;
